import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
const reader = ({ id, this: love}) => (
    <Panel id={id}>
        <header>
        <div className="titlesbiography"  onClick={() => love.goBack()}>
            <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> Назад
        </div>
        </header>
                <div className="hard">
    <div className="test">
        <div dangerouslySetInnerHTML={{__html: love.state.booktext}} />;
    </div>
    </div>
    </Panel>
);
reader.propTypes = {
    id: PropTypes.string.isRequired,
};
export default reader;